/* readable marks */
.editor-content mark { padding: 0 0.08em; border-radius: 2px; color: inherit; }

/* match the legend exactly */
.editor-content mark[data-highlight-color="yellow"] { background-color: #fff3b0 !important; border: 1px solid #facc15 !important; }
.editor-content mark[data-highlight-color="red"]    { background-color: #ffd1d1 !important; border: 1px solid #f87171 !important; }
.editor-content mark[data-highlight-color="blue"]   { background-color: #dbeafe !important; border: 1px solid #93c5fd !important; }

/* legend squares */
.legend-yellow { background-color: #fff3b0; border: 1px solid #facc15; }
.legend-red    { background-color: #ffd1d1; border: 1px solid #f87171; }
.legend-blue   { background-color: #dbeafe; border: 1px solid #93c5fd; }

/* tooltips via data-tip */
.editor-content [data-tip] { position: relative; }
.editor-content [data-tip]:hover::after {
  content: attr(data-tip);
  position: absolute; left: 0; top: 100%; margin-top: 4px;
  max-width: 28rem; white-space: normal; z-index: 20;
  padding: 6px 8px; font-size: 12px; line-height: 1.25;
  color: #111827; background: #f9fafb; border: 1px solid #e5e7eb;
  border-radius: 4px; box-shadow: 0 4px 10px rgba(0,0,0,.08);
}

/* headings and lists */
.editor-content h1 { font-size: 1.5em; margin: .75em 0 .25em; }
.editor-content ul { list-style-type: disc; margin-left: 1.5em; }
.editor-content ol { list-style-type: decimal; margin-left: 1.5em; }
